table.listing {
    border: solid 1px #333;
    border-right: none;
    width: 100%; 
}

table.listing tr:nth-child(2n+1) {
    background-color: #f2f7ff;
}

table.listing tr:nth-child(2n) {
    background-color: #c9dcff;
}

table.listing th {
    background-color: #ccc;
    font-size: 80%;
    text-align: left;
    border-right: solid 1px #333;
}

table.listing td {
    border-right: solid 1px #333;
}