.tile {
    border: solid 1px #333; 
    background-color: #b2c9ff; 
    color: #333; 
    padding: 1vmin 3vmin; 
    cursor: pointer; 
    white-space: pre-line;
}

.tile h3 {
    font-size: 400%; 
    margin: 0;
}

.tile h3.small {
    font-size: 250%; 
}

.tile p {
    font-size: 90%; 
    margin: 0; 
    font-weight: bolder;
}
