html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%; 
  font-size: 2vmin;
}

@media screen and (orientation: portrait) {
  body { 
    font-size: 3.5vmin; 
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #9d71e8;
}

.label {
  font-weight: bold;
  display: block; 
  margin: 0.5rem 0; 
}

#root {
  height: 100%;
}

.page {
  display: flex; 
  flex-direction: column; 
  min-height: 100%;
}

.content {
  padding: 2px 3vmin 5vmin 3vmin; 
  flex-grow: 1;
}

header {
  flex-grow: 0;
  background-color: #c9dcff;
  color: #9d71e8; 
  display: flex; 
  align-items: center;
  padding: 1vmin 5vmin 1vmin 2vmin;
}

@media screen and (orientation: portrait) {
  header { 
    display: block;  /*no flex*/
    text-align: center;
  }
}

header .logo {
  height: 6vmin;
  pointer-events: none;
}

.navigation {
  flex-grow: 1; 
  display: flex; 
  justify-content: flex-end; 
  white-space: nowrap;
}

.navigation ul {
  display: flex;
  gap: 1em;
  list-style-type: none;
  margin: 0; 
  padding: 0; 
}

.navigation a {
  text-decoration: none; 
  display: inline-block; 
  padding: 1vmin; 
}

.navigation a.current {
  background-color: #fff; 
}

@media screen and (orientation: portrait) {
  .navigation { 
    font-size: 3vmin;
  }
}

footer {
  flex-grow: 0;
  background-color: #c9dcff;
  color: #333; 
  padding: 1vmin 5vmin;
  display: grid; 
  grid-template-areas: 'logo about company legal social'; 
  gap: 6vmin; 
  line-height: 1.5em;
}

@media screen and (orientation: portrait) {
  footer { 
    grid-template-areas: 
      'logo about about'
      'company legal social'
    ; 
  }
}

footer h2 {
  font-size: 100%;
}

footer .logo {
  height: 15vmin;
  pointer-events: none; 
  margin-top: 2em;
}

footer ul {
  list-style-type: none; 
  margin: 0; 
  padding: 0; 
}

footer ul li {
  white-space: nowrap; 
}

footer a {
  color: inherit; 
  text-decoration: none; 
}

a.link-button, button.action {
  display: block; 
  padding: 10px 20px; 
  text-align: center; 
  background-color: #9d71e8; 
  color: #000; 
  text-decoration: none; 
  border-radius: 4px; 
  border: solid 1px #333;
}

a.link-button.compact, button.action.compact {
  padding: 0px 2px;
}

input, select {
  font-size: 1.8vmin;
}

.canvasjs-chart-credit { display: none; }  /* there is still the trial version watermark */

