.headline {
    display: flex;
    align-items: center;
    gap: 0.6em;
    margin: 0.5em 0; 
    color: #9d71e8; 
}

.headline .icon {
    height: 1em; 
    position: relative; 
    top: 1px; 
}

.headline h2 {
    font-size: 1em; 
    margin: 0; 
}

@media screen and (orientation: portrait) {
    .headline {
        justify-content: center;
    }
}
