.responsive-grid {
    display: grid; 
    gap: 3vmin; 
    margin: 1vmin 0; 
}

.responsive-grid.compact {
    gap: 1vmin; 
}

.responsive-grid.landscape-12 { grid-template-columns: repeat(12, 1fr); }
.responsive-grid.landscape-11 { grid-template-columns: repeat(11, 1fr); }
.responsive-grid.landscape-10 { grid-template-columns: repeat(10, 1fr); }
.responsive-grid.landscape-9 { grid-template-columns: repeat(9, 1fr); }
.responsive-grid.landscape-8 { grid-template-columns: repeat(8, 1fr); }
.responsive-grid.landscape-7 { grid-template-columns: repeat(7, 1fr); }
.responsive-grid.landscape-6 { grid-template-columns: repeat(6, 1fr); }
.responsive-grid.landscape-5 { grid-template-columns: repeat(5, 1fr); }
.responsive-grid.landscape-4 { grid-template-columns: repeat(4, 1fr); }
.responsive-grid.landscape-3 { grid-template-columns: repeat(3, 1fr); }
.responsive-grid.landscape-2 { grid-template-columns: repeat(2, 1fr); }

@media screen and (orientation: portrait) {
    .responsive-grid.portrait-8 { grid-template-columns: repeat(8, 1fr); }
    .responsive-grid.portrait-7 { grid-template-columns: repeat(7, 1fr); }
    .responsive-grid.portrait-6 { grid-template-columns: repeat(6, 1fr); }
    .responsive-grid.portrait-5 { grid-template-columns: repeat(5, 1fr); }
    .responsive-grid.portrait-4 { grid-template-columns: repeat(4, 1fr); }
    .responsive-grid.portrait-3 { grid-template-columns: repeat(3, 1fr); }
    .responsive-grid.portrait-2 { grid-template-columns: repeat(2, 1fr); }
    .responsive-grid.portrait-1 { grid-template-columns: repeat(1, 1fr); }
}

/* for .wide-content the grid will break into vertical flow also if not enough horizontal space is available: */
@media screen and (max-width: 1200px) {
    .responsive-grid.wide-content.portrait-8 { grid-template-columns: repeat(8, 1fr); }
    .responsive-grid.wide-content.portrait-7 { grid-template-columns: repeat(7, 1fr); }
    .responsive-grid.wide-content.portrait-6 { grid-template-columns: repeat(6, 1fr); }
    .responsive-grid.wide-content.portrait-5 { grid-template-columns: repeat(5, 1fr); }
    .responsive-grid.wide-content.portrait-4 { grid-template-columns: repeat(4, 1fr); }
    .responsive-grid.wide-content.portrait-3 { grid-template-columns: repeat(3, 1fr); }
    .responsive-grid.wide-content.portrait-2 { grid-template-columns: repeat(2, 1fr); }
    .responsive-grid.wide-content.portrait-1 { grid-template-columns: repeat(1, 1fr); }
}
